.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 260px;
  background: #11101d;
  z-index: 100;
  transition: all 0.5s ease;
}
.sidebar .logo-detalle {
  background-image: url(/build/img/logo.webp);
  background-position: 50%;
  background-repeat: no-repeat;
  display: flex;
  height: 85px;
  width: 100%;
  margin-top: 1rem;
}
.sidebar .logo-detalle i,
.sidebar .logo-detalle .logo-texto {
  font-size: 0px;
}
.sidebar .enlace-navegacion {
  height: 100%;
  padding: 10px 0 170px;
  overflow: auto;
}
.sidebar .enlace-navegacion::-webkit-scrollbar {
  display: none;
}
.sidebar .enlace-navegacion li {
  position: relative;
  list-style: none;
  transition: all 0.4s ease;
}
.sidebar .enlace-navegacion li:hover {
  background: #1d1b31;
  color: #ff5900;
}
.sidebar .enlace-navegacion li:hover .sub-menu.blank {
  top: 50%;
  transform: translateY(-50%);
}
.sidebar .enlace-navegacion li .enlace-link {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.sidebar .enlace-navegacion li i {
  height: 50px;
  min-width: 78px;
  text-align: center;
  line-height: 50px;
  color: #FFFFFF;
  font-size: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
}
.sidebar .enlace-navegacion li i:hover {
  color: #ff5900;
}
.sidebar .enlace-navegacion li a {
  display: flex;
  align-items: center;
  text-decoration: none;
}
.sidebar .enlace-navegacion li a .enlace-texto {
  font-size: 18px;
  font-weight: 400;
  color: #FFFFFF;
  transition: all 0.4s ease;
}
.sidebar .enlace-navegacion li .sub-menu {
  background: #1d1b31;
  display: none;
  padding: 10px 10px 10px 60px;
}
.sidebar .enlace-navegacion li .sub-menu a {
  color: #FFFFFF;
  font-size: 15px;
  padding: 5px 0;
  white-space: nowrap;
  transition: all 0.3s ease;
}
.sidebar .enlace-navegacion li .sub-menu a:hover {
  color: #ff5900;
}
.sidebar .enlace-navegacion li .sub-menu .enlace-texto {
  display: none;
}
.sidebar .enlace-navegacion li .sub-menu.blank {
  opacity: 1;
  pointer-events: auto;
  padding: 3px 20px 6px 16px;
  opacity: 0;
  pointer-events: none;
}
.sidebar .enlace-navegacion li.mostrarMenu i.arrow {
  transform: rotate(-180deg);
}
.sidebar .enlace-navegacion li.mostrarMenu .sub-menu {
  display: block;
}
.sidebar .perfil-detalle {
  position: fixed;
  bottom: 0;
  width: 260px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  background: #1d1b31;
  padding: 3px 0;
  transition: all 0.5s ease;
}
.sidebar .perfil-detalle .profile-content {
  display: flex;
  align-items: center;
}
.sidebar .perfil-detalle img {
  height: 52px;
  width: 52px;
  object-fit: cover;
  border-radius: 16px;
  margin: 0 14px 0 12px;
  background: #1d1b31;
  transition: all 0.5s ease;
}
.sidebar .perfil-detalle .nombre_perfil {
  color: #FFFFFF;
  font-size: 18px;
  font-weight: 500;
  white-space: nowrap;
}
.sidebar .perfil-detalle .rol {
  color: #FFFFFF;
  font-size: 18px;
  font-weight: 500;
  white-space: nowrap;
  font-size: 12px;
}
.sidebar .perfil-detalle .logout:hover {
  color: #cb0000;
}

.sidebar.close {
  width: 78px;
}
.sidebar.close .logo-detalle {
  background-image: none;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 65px;
  width: 100%;
  margin-top: 0rem;
}
.sidebar.close .logo-detalle i,
.sidebar.close .logo-detalle .logo-texto {
  font-size: 5rem;
  height: 50px;
  text-align: center;
  line-height: 50px;
}
.sidebar.close .logo-detalle .logo_name {
  transition-delay: 0s;
  opacity: 0;
  pointer-events: none;
}
.sidebar.close .enlace-navegacion {
  overflow: auto;
}
.sidebar.close .enlace-navegacion li .enlace-link {
  display: block;
}
.sidebar.close .enlace-navegacion li a .enlace-texto {
  opacity: 0;
  pointer-events: none;
}
.sidebar.close .enlace-navegacion li .sub-menu {
  position: absolute;
  left: 100%;
  top: -10px;
  margin-top: 0;
  padding: 10px 20px;
  border-radius: 0 6px 6px 0;
  opacity: 0;
  display: block;
  pointer-events: none;
  transition: 0s;
  line-height: 2.5rem;
}
.sidebar.close .enlace-navegacion li .sub-menu .enlace-texto {
  font-size: 18px;
  opacity: 1;
  display: block;
}
.sidebar.close .enlace-navegacion li:hover .sub-menu {
  top: 0;
  opacity: 1;
  pointer-events: auto;
  transition: all 0.4s ease;
}
.sidebar.close .enlace-navegacion i.arrow {
  display: none;
}
.sidebar.close .perfil-detalle {
  background: none;
  width: 78px;
  background: #1d1b31;
  bottom: 0;
  justify-content: flex-start;
  padding: 3px 0;
  position: fixed;
  transition: all 0.5s ease;
}
.sidebar.close .perfil-detalle img {
  padding: 10px;
}
.sidebar.close .perfil-detalle .nombre_perfil {
  display: none;
}
.sidebar.close .perfil-detalle .rol {
  display: none;
}
.sidebar.close ~ .home-section {
  left: 78px;
  width: calc(100% - 78px);
}
.sidebar.close ~ .home-section .flex-carrito {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #11101d;
  display: flex;
  flex-wrap: wrap;
}
.sidebar.close ~ .home-section .home-carrito {
  padding-right: 15px;
}

.home-section {
  position: relative;
  background: #E4E9F7;
  height: 100vh;
  left: 260px;
  width: calc(100% - 260px);
  transition: all 0.5s ease;
}
.home-section .home-content .bx-menu {
  color: #FFFFFF;
  font-size: 35px;
  cursor: pointer;
  margin-right: 10px;
}
.home-section .home-content .text {
  color: #FFFFFF;
  font-size: 35px;
  font-size: 26px;
  font-weight: 600;
}

.home-content {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  background: #11101d;
  padding: 15px;
}

@media screen and (max-width: 400px) {
  .sidebar {
    width: 240px;
  }
  .sidebar .perfil-detalle {
    width: 240px;
  }
  .sidebar.close {
    width: 78px;
  }
  .sidebar.close .perfil-detalle {
    background: none;
    width: 78px;
  }
  .sidebar.close ~ .home-section {
    left: 78px;
    width: calc(100% - 78px);
  }
  .home-section {
    left: 240px;
    width: calc(100% - 240px);
  }
}
.container {
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 30px;
}

.form {
