.modalDialog {
  position: fixed;
  font-family: Arial, Helvetica, sans-serif;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 99999;
  opacity: 0;
  -webkit-transition: opacity 200ms ease-in;
  -moz-transition: opacity 200ms ease-in;
  transition: opacity 200ms ease-in;
  pointer-events: none;
}

.modalDialog:target {
  opacity: 1;
  pointer-events: auto;
}

.modalDialog > div {
  width: 70%;
  position: relative;
  margin: 4% auto;
  padding: 20px;
  border-radius: 10px;
  background: #fff;
  background: -moz-linear-gradient(#fff, #999);
  background: -webkit-linear-gradient(#fff, #999);
  background: -o-linear-gradient(#fff, #999);
  -webkit-transition: opacity 200ms ease-in;
  -moz-transition: opacity 200ms ease-in;
  transition: opacity 200ms ease-in;
}
.modalDialog > div.modal-footer {
  margin: -9% auto;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  gap: 1rem;
}

.cierra {
  background: #cb0000;
  color: #fff;
  font-size: 2rem;
  font-weight: 700;
  height: 30px;
  line-height: 25px;
  padding: 3px;
  position: absolute;
  right: 10px;
  text-align: center;
  text-decoration: none;
  top: 10px;
  width: 30px;
  border-radius: 50%;
}